<template>
  <div id="piecharts2" class="agechart"></div>
</template>

<script>
export default {
  props: {
    ageData: {
      type: Array,
      // eslint-disable-next-line vue/require-valid-default-prop
      default: [
        { value: 80, name: '' },
        { value: 5, name: '' },
        { value: 15, name: '' },
        { value: 25, name: '' },
        { value: 20, name: '' },
        { value: 35, name: '' },
        { value: 30, name: '' },
        { value: 40, name: '' }
      ]
    }
  },

  data () {
    return {
      chart: null,
      option: null
    }
  },
  // pieChartData 
  watch: {
    ageData: {
      handler (newVal, oldVal) {
        console.log('hello')
        if (this.ageData) {
          if (newVal) {
            this.option.series[0].data = newVal
            // this.option.legend.data = newVal.name
            this.chart.setOption(this.option, true)
          } else {
            this.option.series[0].data = this.oldVal
            // this.option.legend.data = this.oldVal.name
            this.chart.setOption(oldVal, true)
          }
        } else {
          this.init()
        }
      },
      deep: true // 
    }
  },

  mounted () {
    this.drawECharts()
  },
  name: 'piechart2',
  methods: {
    drawECharts () {
      let myChart = this.$echarts.init(
        document.getElementById('piecharts2'),
        'temp'
      )
      this.chart = myChart
      this.option = {
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          left: 'center',
          top: 'bottom',
          data: this.ageData.name,
          textStyle: {
            color: 'white'
          }
        },
        toolbox: {
          show: true,
          feature: {
            mark: {show: true},
            magicType: {
              show: true,
              type: ['pie', 'funnel']
            }
          }
        },
        series: [
          {
            name: 'Age Distribution',
            type: 'pie',
            radius: [40, 150],
            center: ['50%', '30%'],
            roseType: 'area',
            label: {
              show: true
            },
            emphasis: {
              label: {
                show: true
              }
            },
            data: this.ageData
          }
        ]

      }

      if (this.option && typeof this.option === 'object') {
        this.chart.setOption(this.option, true)
        window.onresize = function () {
          console.log('resizing now ')
          myChart.resize()
        }
      }
    }
  }
}
</script>

<style scoped>
.agechart {
  width: 100%;
  height: 400px;
}
</style>



